<template>
  <div class="recentPaymoney">
    <!--<div class="title">-->
    <!--<div class="titleLeft" @click="back()"><img src="../../assets/nav_btn_back@2x.png" alt=""></div>-->
    <!--<div class="titleMid"><span></span></div>-->
    <!--</div>-->
    <m-header></m-header>
    <div class="tab">
      <mt-popup
        v-model="popupVisible"
        position="bottom" class="box">
        <div @click.stop="selectState('')"></div>
        <template v-for="item in lists">
          <div @click.stop="selectState(item)">{{item}}</div>
        </template>
        <div id="boxBlank"></div>
        <div @click="popupVisible = false"></div>
      </mt-popup>
      <mt-popup
        v-model="popupVisible2"
        position="bottom" class="box">
        <div @click.stop="selectState2('')"></div>
        <div @click.stop="selectState2('e')">e</div>
        <div @click.stop="selectState2('')"></div>
        <div id="boxBlank"></div>
        <div @click="popupVisible2 = false"></div>
      </mt-popup>
      <div class="tabItem" @click="popupVisible = true">
        <div class="table-left"></div>
        <input class="selectInput" type="text" placeholder="" v-model="timeSelect" readonly>
        <div class="rightIcon"><img src="../../assets/bottom.png" width="26" height="14"></div>
      </div>
      <div class="tabItem" @click="popupVisible2 = true">
        <div class="table-left"></div>
        <input class="selectInput" type="text" placeholder="" v-model="typeSelect" readonly>
        <div class="rightIcon"><img src="../../assets/bottom.png" alt=""></div>
      </div>
      <!--<div class="tabItem" @click="typeTable()">-->
      <!--<span></span><img src="../../assets/bottom.png" alt="">-->
      <!--</div>-->
    </div>
    <div class="prompt2" v-show="promptShow"></div>
    <div class="recordWrapper">
      <div class="play-table" v-for="item in records"  v-show="tableShow">
        <div class="play-table-name">{{item.repayDate | _dateAddHorizontal}}</div>
        <div class="play-table-waitRepay">{{item.amount}}</div>
        <div class="play-table-money"></div>
        <div class="play-table-date">-{{item.productName}},{{item.alreadyRepayTimes}}/{{item.repayTotalTimes}}</div>
        <div class="play-table-count">{{item.id | idstatus}}</div>
        <div class="play-table-outDate" v-show="item.overDueDays !== null && item.overDueDays !== 0">{{item.overDueDays}}</div>
        <div class="autoless"><img src="../../assets/right.png" alt=""></div>
      </div>
    </div>
    <div class="play-table-noRecords" v-show="noRecords"></div>
  </div>
</template>

<script>
  import axios from 'axios'
  import {Toast} from 'mint-ui'
  import {
    currentBorrowAmount,
    borrowRecord,
    recentRepaymentPlan,
    addRecord,
    repaymentPlan,
    timeTableList,
    timeTableListAfter
  } from '../../api/index'
  import {getTime, getDate, dateAddHorizontal} from '../../common/js/times'
  import {} from '../../common/js/jquery-3.2.1.slim.min.js'
  import mHeader from '@/components/Header'
  export default {
    components: {
      mHeader
    },
    data() {
      return {
        isActive1: true,
        records: [],
        lists: [],
        id: '',
        promptShow: true,
        tableShow: true,
        popupVisible: false,
        popupVisible2: false,
        timeSelect: '',
        typeSelect: '',
        data: '',
        noRecords: false,
        tableShow: true
      }
    },
    filters: {
      _getDate(t) {
        return getDate(t)
      },
      _dateAddHorizontal(t) {
        return dateAddHorizontal(t)
      },
      _dataYaerMonth(t) {
        return dataYaerMonth(t)
      },
      idstatus(t) {
        return t === 0 ? '' : t === 1 ? '' : ''
      }
    },
    created() {
      this.accountTel = this.$route.query.accountTel;
      this.recentRepaymentPlan();
      this.timeTableListAfter()
//      this.selectState()
    },
    methods: {
      back() {
        this.$router.goBack()
      },
      //      
      recentRepaymentPlan() {
        let params = {
          mobile: this.accountTel

        }
        recentRepaymentPlan(params).then(res => {
          if (res.code === 0 && res.list.length !== 0) {
            this.records = res.list
            this.noRecords = false
            this.tableShow = true

          } else {
            this.noRecords = true
            this.lookMoreShow = false
          }
        })
      },
      // 
      dateYearMonth(t) {
        let data
        data = t.substr(0, 4) + t.substr(5, 2)
        return data
      },
//      proup
      selectState(state) {
        this.timeSelect = state;
        let time = this.dateYearMonth(state)
        // console.log('dateYearMonth:' + this.dateYearMonth(state))
        this.popupVisible = false;
        if (state == '') {
          this.recentRepaymentPlan()
        } else {
          let params = {
            mobile: this.accountTel,
            applyDate: time
          }

          recentRepaymentPlan(params).then(res => {
            if (res.code === 0 && res.list.length !== 0) {
              this.records = res.list
              console.log(this.records)
              this.noRecords = false
              this.tableShow = true
            } else {
              this.noRecords = true
              this.tableShow = false
            }
          })
        }
      },
      selectState2: function (state) {
        this.typeSelect = state;
        this.popupVisible2 = false;
        if (state == '') {
          this.recentRepaymentPlan()
        }
        if (state == '') {
          this.recentRepaymentPlan()
        }
        if (state == 'e') {
          let params = {
            mobile: this.accountTel

          }
          recentRepaymentPlan(params).then(res => {
            if (res.code === 0 && res.list.length !== 0) {
              this.records = res.list.slice
              this.noRecords = true
            } else {
              this.noRecords = true
            }
          })
        }
      },
      //6
      timeTableListAfter() {
        let params = {
          mobile: this.accountTel
        }
        timeTableListAfter(params).then(res => {
          if (res.code === 0 && res.list.length !== 0) {
            this.lists = res.list;
            this.noRecords = false
            this.lookMoreShow = true
          } else {
            this.noRecords = true
            this.lookMoreShow = false
          }
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "./recentPaymoney.scss";
</style>



